@import url('https://fonts.googleapis.com/css2?family=Secular+One&display=swap');

body {
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  font-family: 'Nanum Gothic', sans-serif;
}

.inner {
  position: relative;
  max-width: 1000px;
  margin: auto;
}

.large-inner {
  position: relative;
  max-width: 1300px;
  margin: auto;
}

a {
  text-decoration: none;
  color: #fff;
  display: block;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 190px;
  height: 50px;
}

.btn:hover {
  color: #fff;
  background-color: #1f2147;
}

