.btn.btn--white {
  color: #1f2147;
  border: #fff;
  background-color: #fff;
}

.btn.btn--white:hover {
  color: #fff;
  background-color: #1f2147;
}

// header
header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 9;
  background-color: #F6F5F0;
  border-bottom: 1px solid #c8c8c8;

  >.inner {

    .navbar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px 12px;

      .navbar__logo a img {
        width: 150px;
      }

      .navbar__menu {
        display: flex;

        li {
          padding: 8px 12px;

          a {
            color: #202147;
          }
        }

        li:hover {
          background-color: #d49466;
          border-radius: 4px;
        }
      }

      .navbar__toogleBtn {
        display: none;
        position: absolute;
        right: 32px;
        font-size: 24px;
        color: #d49466;
      }
    }
  }
}

//video
.video {
  text-align: center;
  margin-top: 100px;

  video {
    width: 100%;
    height: 100%;
  }
}

// Vision
.vision {
  padding: 170px 0 180px;
  background-color: #fff;
  text-align: center;

  >.inner {
    display: flex;
    flex-direction: column;

    .vision-description {
      padding-bottom: 50px;

      h3 {
        font-size: 50px;
        font-weight: 900;
        margin-bottom: 50px;
      }

      p {
        font-size: 24px;
        font-weight: 900;
        color: rgb(3, 1, 143);
        padding: 0 20px;
      }
    }
  }

  .large-inner {
    max-width: 1100px;

    .img-box {
      display: flex;

      .img {
        >p {
          display: none;
        }

        span {
          display: flex;
          justify-content: center;
          font-size: 24px;
          font-weight: 900;
          color: rgb(7, 7, 165);
          padding: 20px;
        }

        img {
          width: 70%;
        }
      }
    }
  }
}

//experience
.experience {
  background-color: #eaeaee;
  padding: 150px 0 120px;

  >.large-inner {
    h3 {
      font-size: 60px;
      font-weight: 900;
    }

    .experience__contents {
      .content__item {
        padding: 80px 0;
        display: flex;
        justify-content: space-between;


        .item__text {
          width: 45%;
          padding-top: 40px;

          h4 {
            font-size: 40px;
            font-weight: 600;
          }

          p {
            font-size: 20px;
            font-weight:500;
            color: #000;
          }
        }

        .item__img {
          width: 45%;

          img {
            width: 100%;
          }
        }
      }
    }
  }
}

// Solution
.solution {
  background-color: #3259f6;
  padding: 180px 0 130px;

  >.inner {
    text-align: center;

    h3 {
      color: #fff;
      font-size: 36px;
      font-weight: 900;
      padding: 0 30px 30px 30px;
    }

    p {
      font-size: 22px;
      font-weight: 900;
      color: #fff;

    }

    .solution-box {
      display: flex;

      >.img {
        padding: 30px;

        .img__btn {
          display: flex;
          justify-content: center;
          padding-top: 30px;

          .btn {
            background-color: #fff;
            color: #202147;
            border: 1px solid #202147;
            font-size: 18px;
            font-weight: 900;
            transition: .3s;
          }

          .btn:hover {
            color: #fff;
            background-color: #1f2147;
          }
        }

        img {
          width: 90%;
          margin-top: 50px;
        }
      }
    }
  }
}

//contact us
.contact {
  padding: 120px 0;

  >.inner {
    display: flex;
    justify-content: space-around;
    padding: 0 20px;
    .contact_text {
      width: 300px;
      >h2 {
        font-family: 'Secular One', sans-serif;
        font-size: 80px;
        line-height: 1;
        padding-bottom: 70px;
      }

      .des {
        color: #777;
      }

      .email {
        padding: 20px 0 5px 30px;
        font-weight: 600;
        font-size: 13px;
      }

      .address {
        font-weight: 600;
        font-size: 13px;
      }

      p {
        padding-left: 30px;
        line-height: 1.6;
        font-size: 15px;
      }
    }

    .contact_text::before {
      content: "";
      width: 3px;
      height: 190px;
      background-color: rgb(220, 177, 177);
      position: absolute;
      top: 0;
      bottom: -200px;
      margin: auto;
    }

    .sign {
      width: 428px;

      input {
        border: none;
        border-bottom: 1px solid #999;
        outline: none;
        padding: 10px 0;
      }

      textarea {
        border: none;
        border-bottom: 1px solid #999;
        outline: none;
        padding: 10px 0;
      }

      label {
        font-weight: 600;
      }

      .first {
        padding-right: 90px;
      }

      .Title {
        display: inline-flex;
        padding-bottom: 40px;
      }

      .bt {
        button {
          background-color: #fff;
          width: 150px;
          border: 1px solid #000;
          border-radius: 30px;
          padding: 12px 25px;
          cursor: pointer;
        }
      }
    }
  }
}

// awards
.awards {
  padding: 40px 0;
  overflow: hidden;
  background-color: #272727;

  .inner {
    .swiper-container {
      width: 100%;
      height: 40px;
    }
  }
}


//footer
footer {
  background-color: #9a9af7;
  position: relative;
  .inner {
    padding: 40px 0 120px 0;

    .info {
      display: none;
      color: #fff;
      font-weight: 900;
      line-height: 1.7;
      position: absolute;
      bottom: 30px;
      left: 0;
    }

    > p {
      position: absolute;
      bottom: 30px;
      right: 0;
      font-size: 20px;
      color: #fff;
    }
  }
}

@media screen and (max-width: 1340px) {

  //experience
  .experience {
    >.large-inner {
      h3 {
        padding-left: 30px;
      }

      .experience__contents {
        .content__item {
          padding: 80px 30px;

          .item__text {
            padding: 0;

            h4 {
              font-size: 35px;
            }

            p {
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}


