@media screen and (max-width: 426px) {

  // Vision
  .vision {
    .large-inner {
      .img-box {
        display: flex;
        flex-direction: column;

        .img {
          position: relative;

          >p {
            display: block;
            padding: 10px 20px;
          }

          img {
            width: 60%;
            padding-top: 100px;
          }

          span {
            position: absolute;
            top: 40px;
            left: 0;
            right: 0;
            margin: auto;
          }
        }

        .img:nth-of-type(1)>img {
          padding-top: 30px;
        }

        .img:nth-of-type(1)>span {
